@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
	/*
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;	
	*/
	background: #f5f5f5;
}

#root {
	min-width: 900px;
}

.main {
	display: flex;
	justify-content: space-around;
}

.list-header {
	list-style: none;
	margin-top: 40px;
}

.list-header li {
	display: inline-block;
	padding-right: 30px;
	padding: 2px;
	width: 150px;
	height: 33px;
	text-align: center;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin-right: 10px;
	cursor: pointer;
	background: #fff;
}

.list-header li:hover, .list-header li:active {
	background: #ccc;
}

.list-header li a {
	text-decoration: none;
	color: black;
	
	opacity: 0.5;
}


.sidenav {
	list-style: none;
	height: 100%; 
	width: 250px; 
	position: fixed;
	z-index: 1;
	top: 0; 
	left: 0;
	background-color: #00cc99; 
	overflow-x: hidden; 
	padding-top: 60px; 
	transition: 0.5s; 
}


.sidenav a {
	padding: 8px 8px 8px 32px;
	text-decoration: none;
	font-size: 20px;
	color: #cdc;
	display: block;
	transition: 0.3s;
}


.sidenav a:hover {
	color: black;
	background: #f5f5f5;
}


.sidenav .closebtn {
	position: absolute;
	top: 0;
	right: 25px;
	font-size: 36px;
	margin-left: 50px;
}


#main {
	transition: margin-left .5s;
	padding: 20px;
}


@media screen and (max-height: 450px) {
	.sidenav {padding-top: 15px;}
	.sidenav a {font-size: 18px;}
}

@media screen and (max-width: 750px) {
	.main { justify-content: flex-end; }
	.list-header { margin-left: 1px; }
	#mySidenav { 
		width: 150px; 
		margin-left: 0px;
	}
	#mySidenav a { 
		font-size: 12px;
	}
	.menu-session {
		width: 100%;
		margin-left: 0px;
	}
	
}

.open-nav {
	background: #00cc99;
	width: 100px;
}

.menu-session {
	width: 75%;
	padding: 30px;
	margin-top: 30px;
	margin-left: 30%;
	margin-right: 30px;
}


.plate-types {
    list-style: none;
    padding: 0px;
}

.plate-types li {
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
    margin-bottom: 10px;
}

/*
.plate-types li:hover {
    border-bottom: 3px solid #00cc99;
}
*/

.plate-types li a {
    text-decoration: none;
    color: black;
    min-width: 150px;
    padding: 6px 10px;
    text-align: center;
}

.plate-types li a:hover {
    border-bottom: 3px solid #00cc99;
}

.select-fields-div {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.select-fields-one {
    display: block;
}

.select-fields-div label {
    font-size: 11px;
    margin-left: 6px;
}

.select-fields {
    height: 35px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin: 5px;
}

.input {
    padding-left: 8px;
}

.data-section {
    background: #ebebeb;
    padding: 10px;
    border-radius: 4px;
}

.data-title {
    border-bottom: 1px solid #ccc;
}

.toggle-sign {
    color: #555;
}

.btn-submit {
    border: 1px solid #ccc;
    width: 150px;
    margin-top: 15px;
    padding: 4px 6px;
    border-radius: 4px;
    background: #00cc99;
}

.btn-submit:hover,
.data-module:hover {
    transition: background .3s;
    background: #40deb4;
    outline: none;
}

.data-module {
    display: flex;
    justify-content: space-between;
    background: #00cc99;
    padding: 5px 10px;
    border-radius: 3px;
    cursor: pointer;
}

@media screen and (min-width: 985px) {
    /*
        Risk Assessment
    */
    .health-rating,
    .health-factor {
        margin-left: 35px;
    }

    .economic-rating,
    .economic-factor {
        margin-left: 89px;
    }

    .probability-rating,
    .probability-factor {
        margin-left: 246px;
    }

    .environmental-rating,
    .environmental-factor {
        margin-left: 58px;
    }

    .overall-rating,
    .overall-factor {
        margin-left: 171px;
    }

    .risk-rating {
        margin-left: 288px;
    }
    
    /*
        Remaining Life And Inspection Interval Assessment
    */
    .corrosion-rate {
        margin-left: 263px;
    }
    .acceleration-factor-pitting {
        margin-left: 160px;
    }
    .adjusted-corrosion-rate {
        margin-left: 197px;
    }
    .remaining-life {
        margin-left: 264px;
    }
    .confidence-factor {
        margin-left: 239px;
    }
    .confidence-factor-adjustement {
        margin-left: 147px;
    }
    .ajusted-confidence-factor {
        margin-left: 182px;
    }
    .before-next-inspection {
        margin-left: 87px;
    }
    .next-inspection {
        margin-left: 258px;
    }
}


.image-session {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 60px;
}
.tanker-header {
	display: flex;
}
.tanker-header span {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
}
.tanker-title {
	font-size: 25px;
	font-weight: bold;
	margin-bottom: 0px;
}
.fa-servicestack {
	font-size: 63px;
	margin-right: 20px;
}
.tanker-summary {
	margin-top: 40px;
}
.tanker-title-summary {
	font-size: 12px;
	margin-top: 0px;
}
.select-tanker {
	width: 220px;
	height: 34px;
	border: 1px solid #ccc;
	margin-bottom: 10px;
	border-radius: 4px;
}
.tanker-table {
	width: 100%;
	border-collapse: collapse;
}
.tanker-table tbody tr {
	border-bottom: 1px solid #ccc;
}
.tanker-table-header {
	background: black;
	min-height: 30px;
	height: 30px;
}
.tanker-table-header th {
	color: #ccc;
}
.tanker-table td, .tanker-table th {
	
	font-size: 11px;
	text-align: center;
}
.cell-fixed-width {
	max-width: 230px;
	min-width: 230px;
}
.cell-fixe-date {
	min-width: 120px;
	max-width: 120px;
}
.rest-cell {
	min-width: 120px;
}
.create-tank-button {
	margin-top: 40px;
}

.create-tank-button button {
	border: none;
	padding: 4px 10px;
	background: #008ae6;
	color: white;
	font-weight: bold;
	border-radius: 3px;
}

.create-tank {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 60px;
}

.form-create-tank {
	position: fixed;
	top: -100%;
	left: 60%;
	transform: translate(-50%, -50%);
	z-index: 1000;
	width: 550px;
	padding: 10px;
	box-shadow: 0 15px 30px rgba(0,0,0,0.80);
	transition: .5s;
	background-image: linear-gradient(lightblue, lightblue, white, white);
	visibility: hidden;
}
.form-create-tank.active {
	visibility: visible;
	top: 50%;
}
.close-form {
	cursor: pointer;
	margin-left: 490px;
}
.create-tank form input {
	height: 45px;
}
.create-tank form textarea {
	resize: none;
}
.create-tank form input,
.create-tank form textarea,
.select-user {
	margin-bottom: 10px;
	border: 2px solid #ccc; 
	outline: none;
	padding-left: 10px;
	border-radius: 3px;
	width: 100%;
}
.dates {
	display: flex;
	justify-content: space-between;
	align-items: space-between;
}
input[type="date"] {
	width: 60px;
}
.date {
	font-size: 12px;
	text-transform: capitalize;
}
.select-user {
	height: 45px;
}
.btn-create-tanker {
	width: 100%;
	border: none;
	background: #008ae6;
	height: 41px;
	border-radius: 3px;
	text-transform: uppercase;
	color: white;
	font-weight: bold;
}
.tankers-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-top: 50px;
}
.tanker-detail {
	background: #fafafa;
	width: 290px;
	padding: 8px;
	border: 1px solid #ccc;
	border-radius: 4px;
	margin: 10px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.tanker-detail h3 {
	font-weight: bold;
	font-size: 25px;
}
.tanker-detail p {
	font-size: 13px;
}
.tanker-description {
	font-size: 20px;
}
.display-dates {
	display: flex;
	justify-content: space-between;
	
}
.display-dates span {
	font-size: 13px;
}
#modify-project {
    font-size: 16px;
    color: blue;
}
.fa-trash-alt {
	color: #eb6931;
	cursor: pointer;
	padding-left: 6px;
}
.fa-trash-alt:hover {
	transition: transform .2s;
	transform: scale(1.2);
}
.tanker-pdf {
	border-top: 2px solid #ccc;
}
.tanker-pdf span {
	font-size: 25px;
	cursor: pointer;
}
.tanker-pdf span:hover {
	transition: transform .2s;
	transform: scale(1.1);
}
.fa-file-pdf, .fa-clone {
	color: red;
	opacity: 0.5;
}
.first-pdf {
	display: flex;
	justify-content: center;
	align-items: center;
	background: #ededeb;
	border-radius: 50%;
	width: 35px;
	height: 35px;
}
.delete-assessment, .tanker-pdf {
	display: flex;
	justify-content: space-between;
	margin-top: 10px;
}
.delete-assessment  {
	margin-top: 20px;
}
.delete-assessment span {
	color: blue;
	cursor: pointer;
}
.delete-assessment span:hover {
	transition: transform .2s;
	transform: scale(1.1);
}

.gen-form-row {
	display: flex;
	justify-content: space-between;
	margin-top: 5px;
	margin-bottom: 5px;
}
input[type="date"] {
	width: 190px;
}
